import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Tab } from 'merc-lib';
import { ROUTING_PATH } from '../const/routing-path.const';
import { TAB_PARAM } from './const/tab-param.const';

@Component({
  selector: 'app-quiz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.scss']
})
export class QuizComponent implements OnInit {
  get TAB_PARAM() { return TAB_PARAM; }
  selectedIndex = 0;
  constructor(
    private router: Router,
    private route: ActivatedRoute,

  ) { }

  ngOnInit(): void {

  }
  /**
   * 取得 param
   * @param callback 取得queryParam後答覆
   */
  private setInitParam(callback: any) {
    this.route.queryParams.subscribe(params => {
      callback(params);
    });
  }
  /** 選取tab序號 */
  selectTab(index: number): void {
    this.selectedIndex = index;
  }
}
