import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DateType, SwiperDatePickerService, TaiwanDatePipe } from 'merc-lib';
import * as moment from 'moment';
import { PageSet } from '../../../core/models/pageset';
import { ITableSetting } from '../../../../../../tc-lib/src/lib/interfaces/table-setting';
import { SharedService } from '../../../shared/shared.service';
import { DEVICE } from '../../../../../../tc-lib/src/lib/const/device.const';
import { ICON_PATH } from '../../const/icon-path.const';
import { mockTable } from './mock';

@Component({
  selector: 'app-check-score',
  templateUrl: './check-score.component.html',
  styleUrls: ['./check-score.component.scss']
})
export class CheckScoreComponent implements OnInit {
  get ICON_PATH(): any { return ICON_PATH; }
  scoreList: any[] = mockTable;
  searchForm!: FormGroup;
  pageSet = new PageSet();
  DEVICE = DEVICE;
  tableSetting: ITableSetting[] = [
    { header: '測驗名稱', primaryKey: 'title' },
    { header: '地區', primaryKey: 'location' },
    { header: '期別', primaryKey: 'class' },
    { header: '測驗日期', primaryKey: 'quiz_date' },
    { header: '狀態', primaryKey: 'status' },
    { header: '測驗成績', primaryKey: 'score' },
  ];
  constructor(
    public sharedService: SharedService,
    private formBuilder: FormBuilder,
    private swiperDatePickerService: SwiperDatePickerService,
    private taiwanDatePipe: TaiwanDatePipe,
  ) { }

  ngOnInit(): void {
    this.handleShowData();
    this.pageSet.initialize(mockTable.length);
    this.initForm();
  }
  private initForm(): void {
    this.searchForm = this.formBuilder.group({
      keyword: [''],
      str_date: [moment().add(-2, 'M').valueOf()],
      end_date: [moment().add(1, 'M').valueOf()],
      display_str_date: [this.taiwanDatePipe.transform(moment().add(-2, 'M').valueOf(), true)],
      display_end_date: [this.taiwanDatePipe.transform(moment().add(1, 'M').valueOf(), true)]
    });
  }
  scrollbarControl(): object {
    if (this.sharedService.userDevice === DEVICE.MOBILE) {
      return { x: '960px' };
    } else {
      return {};
    }
  }
  /**
   * 點選起日
   */
  onSelectStartDateClick(): void {
    const form = this.searchForm.getRawValue();
    const start = moment().add(-2, 'M').valueOf();
    const defaultDate = form.str_date ? form.str_date : start;
    const maxDate = moment().valueOf();

    this.swiperDatePickerService.openDatePicker({ defaultDate, dateType: DateType.startDate }).afterClose.subscribe(date => {
      if (date) {
        // 必須設定結束日期
        this.searchForm.get('display_end_date')?.setValidators([Validators.required]);

        const formatDate = this.taiwanDatePipe.transform(date, true);
        this.searchForm.get('str_date')?.patchValue(date);
        this.searchForm.get('display_str_date')?.patchValue(formatDate);
        // 若選擇的起始日大於原本選擇的到期日就刪除他
        const selectStartDate = date;
        const selectEndDate = this.searchForm.get('end_date')?.value;
        if (moment(selectEndDate).isBefore(selectStartDate)) {
          this.searchForm.get('end_date')?.patchValue('');
          this.searchForm.get('display_end_date')?.patchValue('');
        }
      }
    });
  }
  /**
   * 點選迄日
   */
  onSelectEndDateClick(): void {
    const form = this.searchForm.getRawValue();
    const defaultDate = form.end_date || form.str_date;
    const minDate = form.str_date || null;

    console.log(defaultDate);

    this.swiperDatePickerService.openDatePicker({ defaultDate, minDate, dateType: DateType.endDate }).afterClose.subscribe(date => {
      if (date) {
        // 必須設定開始日期
        this.searchForm.get('display_str_date')?.setValidators([Validators.required]);
        const formatDate = this.taiwanDatePipe.transform(date, true);

        this.searchForm.get('end_date')?.patchValue(date);
        this.searchForm.get('display_end_date')?.patchValue(formatDate);
      }
    });
  }
  onSearchClick(): void {

  }

  /**
   * 清除鈕
   */
  onClearClick(): void {

  }
  /** 換頁 */
  onChangePageIndex(pageIndex: number): void {
    if (pageIndex < 1 || pageIndex > this.pageSet.totalPages) { return; }
    this.pageSet.currentPage = pageIndex;
    this.handleShowData();
    console.log(this.pageSet.currentPage);
  }

  /** 顯示資料 */
  handleShowData(): void {
  this.scoreList = mockTable.slice(
      (this.pageSet.currentPage - 1) * this.pageSet.currentPageSize,
      (this.pageSet.currentPage) * this.pageSet.currentPageSize
    );
  }
}
